import React from 'react'
import {Container} from "react-bootstrap";
import Navibar from "../components/navigationbar";

class Privacy extends React.Component{
    componentDidMount() {
        window.scrollTo({top: 0,behavior: 'smooth'})

        let title = [
            "一、隱私權保護政策的適用範圍",
            "二、個人資料的蒐集、處理及利用方式",
            "三、資料之保護",
            "四、網站對外的相關連結",
            "五、與第三人共用個人資料之政策",
            "六、Cookie之使用",
            "七、隱私權保護政策之修正"
        ]
        let context = [
            "隱私權保護政策內容，包括本網站如何處理在您使用網站服務時收集到的個人識別資料。隱私權保護政策不適用於本網站以外的相關連結網站，也不適用於非本網站所委託或參與管理的人員。",
            "當您造訪本網站或使用本網站所提供之功能服務時，我們將視該服務功能性質，請您提供必要的個人資料，並在該特定目的範圍內處理及利用您的個人資料；非經您書面同意，本網站不會將個人資料用於其他用途。\n" +
            "本網站在您使用服務信箱、問卷調查等互動性功能時，會保留您所提供的姓名、電子郵件地址、聯絡方式及使用時間等。\n" +
            "於一般瀏覽時，伺服器會自行記錄相關行徑，包括您使用連線設備的IP位址、使用時間、使用的瀏覽器、瀏覽及點選資料記錄等，做為我們增進網站服務的參考依據，此記錄為內部應用，決不對外公佈。\n" +
            "為提供精確的服務，我們會將收集的問卷調查內容進行統計與分析，分析結果之統計數據或說明文字呈現，除供內部研究外，我們會視需要公佈統計數據及說明文字，但不涉及特定個人之資料。",
            "本網站主機均設有防火牆、防毒系統等相關的各項資訊安全設備及必要的安全防護措施，加以保護網站及您的個人資料採用嚴格的保護措施，只由經過授權的人員才能接觸您的個人資料，相關處理人員皆簽有保密合約，如有違反保密義務者，將會受到相關的法律處分。\n" +
            "如因業務需要有必要委託其他單位提供服務時，本網站亦會嚴格要求其遵守保密義務，並且採取必要檢查程序以確定其將確實遵守。",
            "本網站的網頁提供其他網站的網路連結，您也可經由本網站所提供的連結，點選進入其他網站。但該連結網站不適用本網站的隱私權保護政策，您必須參考該連結網站中的隱私權保護政策。",
            "本網站絕不會提供、交換、出租或出售任何您的個人資料給其他個人、團體、私人企業或公務機關，但有法律依據或合約義務者，不在此限。\n" +
            "\n" +
            "前項但書之情形包括不限於：\n" +
            "\n" +
            "經由您書面同意。\n" +
            "法律明文規定。\n" +
            "為免除您生命、身體、自由或財產上之危險。\n" +
            "與公務機關或學術研究機構合作，基於公共利益為統計或學術研究而有必要，且資料經過提供者處理或蒐集著依其揭露方式無從識別特定之當事人。\n" +
            "當您在網站的行為，違反服務條款或可能損害或妨礙網站與其他使用者權益或導致任何人遭受損害時，經網站管理單位研析揭露您的個人資料是為了辨識、聯絡或採取法律行動所必要者。\n" +
            "有利於您的權益。\n" +
            "本網站委託廠商協助蒐集、處理或利用您的個人資料時，將對委外廠商或個人善盡監督管理之責。",
            "為了提供您最佳的服務，本網站會在您的電腦中放置並取用我們的Cookie，若您不願接受Cookie的寫入，您可在您使用的瀏覽器功能項中設定隱私權等級為高，即可拒絕Cookie的寫入，但可能會導至網站某些功能無法正常執行 。",
            "本網站隱私權保護政策將因應需求隨時進行修正，修正後的條款將刊登於網站上。"
        ]
        // eslint-disable-next-line
        let toBeReplace = new RegExp("\n","g")

        for(let i = 0;i < title.length;++i){
            let ptitle = document.createElement('H2')
            ptitle.textContent = title[i]

            document.getElementById('policy').appendChild(ptitle)
            document.getElementById('policy').innerHTML += "<p>"+context[i].replace(toBeReplace,"<br>")+"</p>"
        }

    }

    render(){
        return(
            <React.Fragment>
                <h1 style={{textAlign: 'center'}}>隱私權聲明</h1>
                <Navibar />
                <Container>
                    <br />
                    <p>本使用條款對台南一中訂餐網站（https://tnfsa.github.io/）（「本網站」）的所有使用者適用，本網站由全景軟體股份有限公司（以下稱「本公司」）營運。 您對本網站的使用受以下使用條款規範。您使用本網站前，請仔細詳閱以下條款。您使用本網站或本網站的任何部分，即代表您已閱讀本使用條款並同意接受其約束。</p>
                    <div id='policy' />
                </Container>
            </React.Fragment>
        )
    }
}

export default Privacy